@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;500;900&display=swap);
body,html,*{
  padding: 0px;
  margin: 0px;

  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a{
  color: grey;
  text-decoration: none;
}

a:hover{
  color: rgb(92, 92, 92);
}

.home{
    display: flex;
    flex-direction: row;
}

.blueBar{
    width: 100vw;
    height: 15px;
    background-image: linear-gradient(to right, #1463ad , #5698e2);
}

.myName{
    font-weight: 500;
    font-size: 24px;
    color: rgb(68, 68, 68);
    margin: 0vh 2vw 2vh 2vw;
    padding-top: 5vh;
}

.modifiedHR{
    margin-left: 2vw;
    width: 15vw;
    min-width: 150px;
    height: 2px;
    border: none;
    color: rgb(204, 204, 204);
    background-color: rgb(230, 230, 230);
}

.linkTo{
    font-weight: 500;
    margin-left: 2vw;
    margin-top: 2em;
    text-decoration: underline;
}

.links{
    position: fixed;
    width: 33vw;
    background-color: rgb(249, 249, 249);
    height: 100vh;
}

.sectionTwo{
    margin-left: 33vw;
    width: 77vw;
    height: 100vh;
}

@media only screen and (max-width: 600px) {
    .linkTo, .modifiedHR, .myName{
        margin-left: 10vw;
    }

    .home{
        display: flex;
        flex-direction: column;
    }

    .links{
        width: 100vw;
        height: 100%;
        padding-bottom: 2em;
        position: relative;
    }

    .sectionTwo{
        width: 100vw;
        margin-left: 0vw;;
    }

  }
.A{
    width: 60%;
    margin-left: 10%;
}

.A h1{
    padding-top: 5vh;
    color: rgb(34, 34, 34);
}

.A h2{
    font-weight: 500;
    margin-top: 5vh;
}

.A h3{
    font-weight: 500;
    color: rgb(34, 34, 34);

}

.note{
    color: grey;
    font-style: italic;
}

ul{
    margin-top: 1vh;
    margin-left: 2em;
}

li{
    font-size: 17px;
    padding: 1em 0em 1em 0em;
}


@media only screen and (max-width: 600px) {
    .protocol{
        width: 80vw;
    }
}
.imagetext{
    display: flex;
    flex-direction: row;
}

.imagetext h3{
    margin-left: 1em;
    width: 70%;
}

@media only screen and (max-width: 600px) {

    .imagetext{
        display: flex;
        flex-direction: column;
    }

    .imagetext h3{
        margin-left: 0em;
        width: 90%;
        padding-left: 5%;

    }

    .imagetext img{
        margin-bottom: 1em;
    }
    
    .E{
        text-align: center;
        display: flex;
        flex-direction: column;
        width: 80%;
        padding-left: 10%;
        margin-left: 0vw;
    }

    .lex{
        max-height: 300px; 
    }

    .E h1{
        padding-bottom: 1em;
    }
}



